/**
 * @module font/fontsize
 */

import Plugin from '@ckeditor/ckeditor5-core/src/plugin';
import Obsintappediting from './obsintappediting';
import Obsintappui from './obsintappui';

export default class ObsIntApp extends Plugin {
    /**
     * @inheritDoc
     */
    static get requires() {
        return [ Obsintappediting, Obsintappui ];
    }

    /**
     * @inheritDoc
     */
    static get pluginName() {
        return 'ObsIntApp';
    }
}
