import Plugin from '@ckeditor/ckeditor5-core/src/plugin';

import {upcastElementToAttribute} from '@ckeditor/ckeditor5-engine/src/conversion/upcast-converters';
import Obsintappcommand from './obsintappcommand';
import Colors from './colors.js';

const TEXT_COLOR = 'obsintappColor';

export default class Obsintappediting extends Plugin {
    /**
     * @inheritDoc
     */
    constructor(editor) {
        super(editor);

        // Define default configuration using named presets.
        editor.config.define(TEXT_COLOR, Colors);
    }

    /**
     * @inheritDoc
     */
    init() {
        const editor = this.editor;

        // Allow textColor attribute on text nodes.
        editor.model.schema.extend('$text', {allowAttributes: TEXT_COLOR});

        // Define view to model conversion.
        const options = editor.config.get('obsintappColor.options');
        const definition = _buildDefinition(options);

        // Set-up the two-way conversion.
        editor.conversion.attributeToElement(definition);

        editor.conversion.for('upcast')
            .add(upcastElementToAttribute({
                view: {
                    name: 'span',
                    attributes: {
                        title: 'color'
                    }
                },
                model: {
                    key: TEXT_COLOR,
                    value: viewElement => {
                        const color = viewElement.getStyle('color');

                        if (!color) {
                            return null
                        }

                        // determine closest color
                        const regEx = /^rgb\((\d*),\s?(\d*),\s?(\d*)\)$/;
                        let rgb = regEx.exec(color);
                        if (!rgb) {
                            return null;
                        }
                        let best = findEqual(options, toHex(rgb[1], rgb[2], rgb[3]));

                        if (!best) {
                            return null;
                        }

                        return best.model;
                    }
                },
                converterPriority: 'highest'
            }));

        // Add TextColor command.
        editor.commands.add(TEXT_COLOR, new Obsintappcommand(editor));
    }
}

function _buildDefinition(options) {
    const definition = {
        model: {
            key: TEXT_COLOR,
            values: []
        },
        view: {},
        upcastAlso: {}
    };

    for (const option of options) {
        definition.model.values.push(option.model);
        definition.view[option.model] = {
            name: 'span',
            classes: option.model,
            priority: 5
        };
    }

    return definition;
}

function toHex(r, g, b) {
    return '#' + parseInt(r).toString(16).padStart(2,'0') + parseInt(g).toString(16).padStart(2,'0') + parseInt(b).toString(16).padStart(2,'0');
}

function findEqual(options, value) {
    let equal = options.filter(function(option) {

        return option.color === value;
    });

    return equal.length ? equal[0] : null;
}